html, body {
    background-color: #fbfbfb;
}

h1, h2, h3, h4, h5 {
    color: #114685;
}


.dropdown-menu.dropdown-menu-right.show {
    z-index:99999;
}

.sd-grid-extra {
    h1,h2,h3,h4,h5 {
        color: #6e89aa;
    }
}

.sd-homepage {
    width:100%;
    min-height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../images/cover-photo.jpg');
    background-size: cover;
    position:relative;
}

.sd-title-container {
    padding: 120px;
    border: solid 2px white;
    color: #fff;
    text-align: center;
    font-size: 4em;
    background: rgba(0,0,0,0.3);

    h1, h2 {
        color: #fff;
        text-align: center;
    }

    h1 {
        font-size: 1.6em;
    }

    h2 {
        font-size: 1em;
    }
}

.sd-image-credit {

    position: absolute;
    bottom: 0;
    left: 0;
    color: #fff;
    padding: 1.5em 1.5em 2em;
    background-color: rgba(0,0,0,0.55);
    width:100%

}

.sd-sidebar-logo--image {
    max-width: 260px;
    background-color: #193f80;
    padding: 5px 10px;
}

.sd-sidebar--sfb-logo {
    height: 100px;
    width: auto;
    padding-right: 0.5rem;
}

.sd-sidebar--sfb-text {
    padding-left: 0.5rem;
}

.sd-sidebar--sfb-link {
    display:flex;
}

.sd-sidebar--sib-logo {
    max-width: 240px;
}

.sd-sidebar--substance-logo {
    max-width: 240px;
}

.sdash-about--infographic {
    display:block;
    box-sizing: border-box;
    max-width:75%;
    margin: 3rem auto;
}